import React from 'react';
import logo from '../../images/logo.png';

const hospitalNameStyle = {
  fontWeight: 'bold',
  fontSize: '18px',
  float: 'left',
  marginLeft: '15px',
};

const Footer = () => {
  return (
    <div style={{ width: '1200px', borderTop: '1px solid gray', height: '150px', marginTop: '-200px' }}>
      <img src={logo} alt="hospital_logo" style={{ width: '100px', margin: '10px', float: 'left' }} />
      <p>
        <p style={hospitalNameStyle}>    <span style={{ fontSize: '24px'}}><b style={{ color: '#ffbb94'}}></b> </span></p>
        <p style={{fontWeight: 'bold', marginLeft: '100px', float: 'left', lineHeight: '30px' }}>(00000)    1(1)</p>
      </p>
      <br />
      <br />
      <br />
      <p style={{ float: 'left', marginLeft: '-270px', marginTop: '0' }}><b> </b> <span style={{ fontSize: '20px'}}>(02) 1234 - 1111 - 2222</span></p>
      <p style={{ marginTop: '0'}}>  : ( 2) 2  150m </p>
    </div>
  );
};

export default Footer;
