import React from 'react';
import {Button, Layout, Menu} from 'antd';
import logo from '../../images/logo.png';
import './style/Navbar.css';
import {Link} from "react-router-dom";

const { Header } = Layout;
const { SubMenu } = Menu;

const NavBar = () => {
  return (
    <Layout style={{ backgroundColor: '#fff' }}>
      <Header
        style={{
          display: 'flex',
          alignItems: 'center',
          height: '100px',
          backgroundColor: '#fff',
          justifyContent: 'space-around',
        }}
      >
        <div>
          <a href="/">
            <img
              src={logo}
              alt="hospital logo"
              style={{position: 'relative', top: '15px', left: '5px', width: '100px'}}/>
          </a>
        </div>
        <Menu
          className="nav-menu"
          theme="light"
          mode="horizontal"
          defaultSelectedKeys={['Home']}
          style={{
            flex: 1,
            justifyContent: 'space-around',
            minWidth: '900px',
            width: '900px',
            border: 'none'
          }}
        >
          <SubMenu key="Introduct" title="">
            <Menu.Item key="doctor"><a href="/doctor"> </a></Menu.Item>
            <Menu.Item key="hospital"><a href="/hospital"> </a></Menu.Item>
          </SubMenu>
          <SubMenu key="surgary-booking" title="">
            <Menu.Item key="booking"><Link to="/booking"></Link></Menu.Item>
            <Menu.Item key="surgery-list"><a href="/surgery-list"> </a></Menu.Item>
          </SubMenu>
          <Menu.Item key="">
            <a href="/"></a>
          </Menu.Item>
          <SubMenu key="customer-center" title="">
            <Menu.Item key="notice"><a href="/notice"></a></Menu.Item>
            <Menu.Item key="event"><a href="/event"></a></Menu.Item>
            <Menu.Item key="qna"><a href="/qna">QnA</a></Menu.Item>
          </SubMenu>
          <Menu.Item key="">
            <a href="/"></a>
          </Menu.Item>
        </Menu>

        <Button style={{ backgroundColor: '#00bfff'}}></Button>
        <Button style={{ marginLeft: '10px', backgroundColor: '#fffff0' }}></Button>
      </Header>
    </Layout>
  );
};
export default NavBar;