import React from 'react';
import { Button, Input } from 'antd';
import './style/Quick_view.css';

const guideTextStyle = {
  width: '150px',
  textAlign: 'center',
  color: '#aaa',
  fontWeight: 'bold',
  float: 'left',
  marginTop: '12px'
};


const Quick_view = () => {
  return (
    <div style={{width: '1200px', height: '50px', backgroundColor: '#fffedd', marginBottom: '15px', position: 'relative', zIndex: 999}}>
      <div className="quick_view_quide">
        <p style={guideTextStyle}> </p>
        <p style={{color: '#aaa', border: '1px solid gray', width: '.1px', height: '40px', float: 'left', marginTop: '5px'}}></p>
        <Button shape='round' style={{ border: 'none', backgroundColor: '#f0e68c', marginTop: '10px', marginLeft: '10px', float: 'left'}}></Button>
        <p style={{float: 'left', marginLeft: '20px', marginTop: '15px'}}>2024-06-29 ()</p>
        <p style={{float: 'left', marginLeft: '40px', marginTop: '15px'}}>1  (: )</p>
        <p style={{float: 'left', marginLeft: '40px', marginTop: '15px', fontWeight: 'bold', color: '#aaa', fontSize: '.9em'}}>()</p>
      </div>
      <div>
        <p style={{ position: 'relative', top: '-70px', right: '-600px' }}>
          <Input placeholder='  RFID ' style={{ width: '205px' }}/>
          <Button htmlType="submit" style={{marginLeft: '10px', border: '2px solid #00bfff', color: '#00bfff', fontWeight: 'bold', padding: '15px 15px'}}></Button>
        </p>
      </div>
    </div>
  );
};

export default Quick_view;
