import React from 'react';
import { Card, Button } from 'antd';
import './style/Login.css';
import {Link} from "react-router-dom";

const Login = () => {

  return (
    <div className="loginWrap">
      <Card style={{ width: '350px', height: '470px', float: 'right', right: '60px', top: '-425px', zIndex: '1' }}>
        <div style={{ textAlign: 'center', fontWeight: 'bold', fontSize: '1.2rem'  }}>
          ,  .
        </div>
        <div>
          <Button type="default"
                  style={{cursor: 'none', border: '2px solid #9ee7ff', color: '#9ee7ff', fontWeight: 'bold', marginTop: '15px', marginLeft: '-150px'}}>
             
          </Button>
        </div>
        <Card
          title=" (2_)"
          style={{
            marginTop: '10px',
            width: '300px',
            height: '290px',
            border: '2px solid #00bfff',
          }}
        >
          <p>  .</p>
          <Link to='/booking'>
            <Button style={{border: '2px solid #00bfff', fontWeight: 'bold', fontSize: '1.1rem', backgroundColor: '#00bfff', color: '#fff', float: 'right' }}></Button>
          </Link>
        </Card>
        <div style={{textAlign: 'center', marginTop: '15px'}}>
          <Button style={{border: '2px solid #f0e68c', color: '#f0e68c', fontWeight: 'bold', marginRight: '10px', padding: '20px 30px'}}> </Button>
          <Button style={{border: '2px solid #00bfff', color: '#00bfff', fontWeight: 'bold', padding: '20px 30px'}}>  </Button>
        </div>
      </Card>
    </div>
  );
};

export default Login;
